<template>
  <div class="magic-ui-shape t-inline-block">
    <div class="shape-body" :style="bodyStyle" />
  </div>
</template>

<script lang="ts" setup>
import { computed } from 'vue';

import { useGetBackGround, useGetBoxShadow } from '../../hooks/useGetStyle';
import { MShape } from '../../types';
import useApp from '../../useApp';
import { ShapeType } from '../enum';
const props = defineProps<{
  config: MShape & {
    backgroundColor?: string;
    backgroundImage?: string;
    radioGroup?: number;
    borderType: string;
    borderColor: string;
    polygon: string;
  };
  model?: Object;
}>();

useApp(props);

// 获取形状外形
const style = computed(() => {
  const { shape } = props.config;
  console.log(shape,'clipPath')
  const bodyStyle: Record<string, string> = {
    width: '100%',
    height: '100%',
  };

  switch (shape) {
    case ShapeType.rectangle:
      bodyStyle.borderRadius = '0';
      break;
    case ShapeType.circle:
      bodyStyle.borderRadius = '50%';
      break;
    case ShapeType.triangle:
      bodyStyle.clipPath = 'polygon(50% 0px, 100% 100%, 0px 100%, 50% 0px)';
      break;
    case ShapeType.line:
      bodyStyle.height = '1px';
      bodyStyle.position = 'absolute';
      bodyStyle.top = '50%';
      bodyStyle.transform = 'translateY(-50%)';
      break;
    case ShapeType.customize:
      bodyStyle.clipPath = props.config.polygon;
      break;
  }
  console.log(bodyStyle.clipPath, 'bodyStyle.clipPath');
  return bodyStyle;
});

const bodyStyle = computed(() => {
  const { boxShadowStyle } = useGetBoxShadow(props.config);
  const { backgroundStyle } = useGetBackGround(props.config);
  return {
    ...style.value,
    ...backgroundStyle,
    ...boxShadowStyle,
  };
});
</script>
<style lang="scss" scoped>
.t-inline-block {
  display: inline-block;
}
.magic-ui-shape {
  overflow: hidden;
}
.shape-body {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}
</style>
